.wp-block-zolo-slide {
    box-sizing: border-box;
    background-color: var(--zoloblocks-item-bg-color);
    position: relative;
    z-index: 1;
    &::before {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        content: '';
        z-index: -1;
    }

    height: 500px;
}
